/*
 * General Variables
 * Note: colors should go into
 * components/00-base/01-colors/_color-vars.css
 * and breakpoint related vars into
 * components/00-base/base/_breakpoints.css
*/
/*
 * Use this on the outer wrapper of page-level elements.
 * It ensures consistent spacing between elements on the page.
 */
.spacing td:not(:last-child) {
  width: 14rem;
}

.spacing-visualization__wrapper {
  display: inline-flex;
  flex-flow: row nowrap;
}

.spacing-visualization {
  width: 3rem;
  height: 3rem;
  display: block;
  border-radius: 4px;
  background-color: rgba( var(--c-text, 24, 25, 27), 1 );
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.25);
}

.sb-show-main {
  background-color: rgba( var(--c-background, 255, 255, 255), 1 );
  color: rgba( var(--c-text, 24, 25, 27), 1 );
  border-top: 1rem solid rgba( var(--c-accent, 111, 67, 154), 1 );
}

.cl-colors {
  padding: 1rem;
}

.cl-colors__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0 0 calc(1rem * 2);
  padding: 0;
}

.cl-colors__item {
  list-style: none;
  padding: 1rem;
  transition: all 0.4s;
  flex: 1 1 20%;
  min-width: 20rem;
  min-height: 18rem;
  display: flex;
  flex-flow: column wrap;
  align-items: flex-start;
  justify-content: flex-start;
  font-weight: bold;
}

.cl-colors__title {
  font-size: 120%;
  padding: calc(1rem / 4) 0;
  display: block;
  line-height: 1;
}

.cl-colors__swatch {
  display: block;
  width: 100%;
  padding: calc(1rem * 3);
  margin: 0 0 calc(1rem / 2);
}

.cl-colors__definition {
  display: block;
  width: 100%;
  padding: calc(1rem / 4);
  font-weight: normal;
  color: rgba( var(--c-text, 24, 25, 27), 1 );
  white-space: pre-wrap;
}

.cl-colors__definition::before, .cl-colors__definition::after {
  display: none;
}

.cl-colors__definition_item {
  display: block;
  cursor: pointer;
  color: rgba( var(--c-text, 24, 25, 27), 0.8 );
  margin: 0 0 calc(1rem / 4);
  padding: calc(1rem / 4);
  background-color: rgba( var(--c-text, 24, 25, 27), 0.05 );
}

.cl-colors__definition_item:hover {
  color: rgba( var(--c-text, 24, 25, 27), 1 );
  background-color: rgba( var(--c-text, 24, 25, 27), 0.1 );
}

.cl-colors__list--default {
  background-color: rgba( var(--c-background, 255, 255, 255), 1 );
  padding: 0 0 calc(1rem * 2);
  color: rgba( var(--c-text, 24, 25, 27), 1 );
}

.cl-colors__item--primary > .cl-colors__swatch {
  background-color: rgba( var(--c-primary, 17, 125, 98), 1 );
  box-shadow: 0 2px 10px rgba( var(--c-neutral-n100, 145, 146, 146), 0.5 );
}

.cl-colors__item--primary > .cl-colors__definition::before {
  content: "Usage: clr(primary, )";
}

.cl-colors__item--primary > .cl-colors__definition::after {
  content: "#117D62<br>rgba(17, 125, 98, 1)<br>hsla(165deg, 76.0563380282%, 27.8431372549%, 1)";
}

