//FUENTE TIPOGRAFICA DE LA PÁGINA
@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,100..700;1,100..700&display=swap');
body{
    font-family: "Josefin Sans", system-ui !important;
    font-optical-sizing: auto;
    font-weight: 500;
    font-style: normal;
    div.main{
        background-position: center;
        background-repeat: repeat-y;
        background-size: 100%;
        margin-bottom: unset;
        overflow-x: hidden;
    }
}

// This file can be used to import all other styles.
/*HEADER PRIMARIO*/
.headerprimario{
    background-color: #2d1d1d;
    @media (max-width:768px){
        display: none;
    }
    .row{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        .col-4{
            .nav{
                display: flex;
                flex-direction: row;
                flex-wrap: nowrap;
                justify-content: flex-end;
                align-items: center;
                gap: 10px;
                .nav-item{
                    .nav-link{
                        color: #fff;
                        background-color: #000;
                        margin: 5px;
                        font-size: 11px;
                        border: 1.5px solid #fff;
                    }
                }
            }
        }
    }
}
/*ESTILOS PARA EL HEADER*/
.header{
    img{
        max-width: 60px;
    }
}
/*ESTILOS PERSONALES PARA EL MENÚ PRINCIPAL*/
.menu-ppal{
    background-color: #000;
    .container-fluid{
        flex-direction: column;
        @media (max-width:990px){
            flex-direction: row;
        }
        .navbar-brand{
            margin: unset;
            img{
                max-width: 130px;
                width: 100%;
                @media (max-width:768px){
                    max-width: 70px;
                }
            }
        }
        .navbar-toggler{
            color: #fff;
        }
        #navbarNavDropdown{
            align-content: center;
            justify-content: center;
            align-items: center;
            .navbar-nav{
                gap: 1rem;
                background-color: #000;
                .nav-item{
                    text-transform: uppercase;
                    font-weight: 600;
                    height: 125px; 
                    min-width: 126px;
                    display: flex;
                    flex-direction: row;
                    flex-wrap: wrap;
                    align-content: center;
                    justify-content: center;
                    align-items: center; 
                    &:hover, &.active{
                        background-color: #fff;
                        .nav-link{
                            color: red;
                        }
                    }
                    .nav-link{
                        color: #fff;
                        text-decoration: underline;
                        width: 100%;
                        height: 100%;
                        display: flex;
                        flex-direction: row;
                        flex-wrap: nowrap;
                        justify-content: flex-start;
                        gap: 10px;
                        align-items: center;
                        &:hover,&.active{
                            background-color: #fff;
                            .nav-link{
                                color: red;
                            }
                        }
                    }
                    .dropdown-menu{
                        &.show{
                            top: 100%;
                            background-color: #000;
                        }
                        .nav-item{
                            height: fit-content;
                            color: #fff;
                        }
                    }  
                    
                    
                }
            }
            &.collapse.show{
                .navbar-nav{
                    gap: 0;
                    .nav-item{
                        height: fit-content;
                        .nav-link{
                            justify-content: flex-start;
                            padding: 1rem;
                        }
                        &.dropdown{
                            justify-content: flex-start;
                            .dropdown-menu{
                                padding: 1rem;
                                width: 100%;
                            }
                        }
                    }
                }
            }
        }
    }
}

/*ESTILOS PARA EL MODAL DEL HOME*/

#popuphome{
    .modal-body{
      padding: 0;
      .btn-close{
        position: absolute;
        right: 0px;
        top: 0px;
        font-size: 2rem;
        background-color: white;
        box-shadow: inset 0px 0px 8px 4px red;
      }
    }
}

/*ESTILOS FOOTER,  MARCA REGISTRADA*/
footer.footer{
    background-color: #000;
    box-shadow: 0 -8px 15px -1px #e21a23;
    .footer__menu{
        .footerMenus{
            padding: 4rem 1rem 1rem 1rem;
            .contac-redes{
                display: flex;
                flex-direction: column;
                justify-content: flex-start;
                align-items: center;
                a.btn{
                    background-color: #fff;
                    color: #000;
                    border: 2px solid #000;
                    text-transform: uppercase;
                    padding: 0.5rem 1rem;
                    margin: 1rem;
                }
                ul.navbar-nav{
                    display: flex;
                    flex-direction: row;
                    flex-wrap: wrap;
                    align-content: center;
                    justify-content: center;
                    align-items: center;
                    gap: 1rem;
                }
            }
            .col-12{
                @media (max-width:768px) {
                    border-top: 1px solid #fff;
                    margin-top: 1rem;
                    padding: 1rem;
                }
                h3{
                    text-transform: uppercase;
                    line-height: 25px;
                    font-weight: 300;
                }
            }
        }
        #block-tragelsports-theme-footermarcaregistrada{
            .marcaRegistrada{
                background-color: #000;
                @media (max-width:768px) {
                    border-top: 1px solid #fff;
                    margin-top: 1rem;
                    padding: 1rem;
                }
                .row{
                    display: flex;
                    flex-direction: row;
                    flex-wrap: wrap;
                    justify-content: space-between;
                    align-items: center;
                    .col-12.col-md-6{
                        max-height: 48px;
                        display: flex;
                        flex-direction: row;
                        justify-content: flex-start;
                        align-items: center;
                        .logo__link{
                            width: 100%;
                            height: 100%;
                            display: flex;
                            flex-direction: row;
                            justify-content: flex-end;
                            align-items: center;
                            img.logo__image{
                                width: 100%;
                                height: auto;
                                max-width: 48px;
                            }
                        }
                    }
                }
            }
        }
    }
}

/*ESTILOS PARA LOS TÍTULOS DE LAS PÁGINAS*/
div#block-tragelsports-theme-page-title {
    position: relative;
    top: -18px;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    z-index: 1;
    @media (min-width:768px){
        top: 180px;
    }
    .heading{
        font-size: 2rem;
        line-height: 85px;
        word-break: break-word;
        margin: unset;
        text-align: center;
        @media (min-width:768px){
            font-size: 7rem;
        }
    }
}

/*ESTILOS PARA LAS MIGAS DE PAN*/
.region-breadcrumb{
    max-width: 768px;
    margin: auto;
    nav{
        ol{
            margin-bottom: unset;
            li{
                font-size: 1rem;
                a{
                    color: #fff !important;
                    font-weight: 800;
                    &::after{
                        content: "/";
                    }
                } 
            }
        }
    }
}

/*ESTILOS PARA ACCIONES*/
#block-tragelsports-theme-local-tasks{
    background: #fff;
    z-index: 99;
}